import React from 'react';
import styles from './filterSelect.module.css'

const FilterSelect = ({ text, option, handleFilter }) => {

	const handleFilterInput = (e) => {
		handleFilter(e.target.value, option)
	}

	const handleFilterSelect = (e) => {
		handleFilter(text, e.target.value)
	}

	return (
		<div className={styles.container}>
			<input className={styles.input} type="text" value={text} onChange={handleFilterInput} placeholder="" />
			<select className={styles.select} value={option} onChange={handleFilterSelect}>
				<option value=""></option>
				<option value=""></option>
				<option value=""></option>
				<option value=""></option>
				<option value=" "> </option>
				<option value=""></option>
				<option value=""></option>
				<option value=""></option>
				<option value=""></option>
				<option value=" "> </option>
				<option value=""></option>
			</select>
		</div>
	);
}

export default FilterSelect;