import React, { useState } from 'react';

import Button from '../../../common/components/Button';

import Input from '../components/Input';
// import RadioBtn from './components/RadioBtn';
import RadioBtn from '../components/RadioBtn';
import styles from './formProject.module.css';

const FormProject = () => {
	const [formData, setFormData] = useState({
		title: '',
		email: '',
		description: '',
		details: '',
		location: '',
		contact: '',
		category: ''
	});

	const { title, email, description, details, location, contact, category } = formData;

	const handleChange = e => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const handleSubmit = async e => {
		e.preventDefault();
		if (title && email && description && details && location && contact && category) {
			const response = await fetch('http://localhost:8080/api/charityProject/', {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Authorization': `Bearer ${localStorage.getItem('token')}`,
				},
				body: JSON.stringify(formData)
			});

			if (response.ok) {
				setFormData({
					title: '',
					email: '',
					description: '',
					details: '',
					location: '',
					contact: '',
					category: ''
				});
				window.location.href = '/charity-project';
			} else {
				alert('   ');
			}
		} else {
			alert(',   ');
		}
	};

	return (
		<div className={styles.wrapper}>
			<div className={styles.name}>
				<h2> </h2>
				<p>    //    .         '    .</p>
			</div>
			<form onSubmit={handleSubmit}>
				<Input label="" name="title" type="text" value={title} onChange={handleChange} />
				<Input label="Email" name="email" type="email" value={email} onChange={handleChange} />
				<Input label=" " name="description" type="text" value={description} onChange={handleChange} />
				<Input label="" name="details" type="text" value={details} onChange={handleChange} />
				<Input label="" name="location" type="text" value={location} onChange={handleChange} />
				<Input label="" name="contact" type="text" value={contact} onChange={handleChange} />

				<RadioBtn label="" name="category" value={category} onChange={handleChange} options={['', '', '', ' ', '', '', '', '', ' ', '']} />
				<button className={styles.submit} type="submit"></button>
			</form>
		</div>
	);
};

export default FormProject;