import React, { useState } from 'react';

import Button from '../../../common/components/Button';

import { useLocation } from 'react-router-dom';
import Input from '../components/Input';
// import RadioBtn from './components/RadioBtn';
import RadioBtn from '../components/RadioBtn';
import styles from './formRequest.module.css';

const FormRequest = ({ typeForm }) => {
	const offerDescr = {
		title: ' ',
		text: '    //    .             .'
	}
	const requestDescr = {
		title: ' ',
		text: '       ,     .'
	}
	const [formData, setFormData] = useState({
		email: '',
		location: '',
		title: '',
		category: '',
		description: '',
		contact: '',
		type: typeForm
	});

	const { email, location, title, category, contact, description } = formData;

	const handleChange = e => {
		setFormData({ ...formData, [e.target.name]: e.target.value });
	};

	const handleSubmit = async e => {
		e.preventDefault();
		if (email && location && title && category && contact && description) {
			const response = await fetch(`http://localhost:8080/api/helpRequest/`, {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json',
					'Authorization': `Bearer ${localStorage.getItem('token')}`,
				},
				body: JSON.stringify(formData)
			});

			if (response.ok) {
				setFormData({
					email: '',
					location: '',
					title: '',
					category: '',
					description: '',
					contact: '',
				});
				window.location.href = '/help-request';
			} else {
				alert('   ');
			}
		} else {
			alert(',   ');
		}
	};

	return (
		<div className={styles.wrapper}>
			<div className={styles.name}>
				<h2>{typeForm === 'offer' ? offerDescr.title : requestDescr.title}</h2>
				<p>{typeForm === 'offer' ? offerDescr.text : requestDescr.text}</p>
			</div>
			<form onSubmit={handleSubmit}>
				<Input label="" name="title" type="text" value={title} onChange={handleChange} />
				<Input label="Email" name="email" type="email" value={email} onChange={handleChange} />
				<Input label="" name="location" type="text" value={location} onChange={handleChange} />
				<Input label="" name="description" type="text" value={description} onChange={handleChange} />
				<RadioBtn label="" name="category" value={category} onChange={handleChange} options={['', '', '', ' ', '', '', '', '', ' ', '']} />
				<Input label="" name="contact" type="text" value={contact} onChange={handleChange} />
				<button className={styles.submit} type="submit"></button>
			</form>
		</div>
	);
};

export default FormRequest;